/**
 * Promise.all(iterable)  ,   ,   
   ,     ,   
    . 
  Promise.all      ,    .
         ,
      . 
 *
 */

//  
export const makeConcurrentRequest = async (url) => {
  const res = await Promise.all(
    url.map((result) => {
      return fetch(result).then((result) => result.json());
    })
  );
  return res;
};
